:root {
  --calendarHorizontalLineHeight: 60px;
}

.TimeLineCalendar_container{
  height: 100%;
  position: relative;
}

.rct-header-root {
  background-color: #ffffff !important;
  color: var(--beUpColor);
  font-weight: 500;
}

.rct-left-group-header {
  display: flex;
  align-items: center;
  border-right: 1px solid var(--tableGridColor);
}

.rct-calendar-header {
  display: flex;
  border: 0 !important;
}

.rct-calendar-header > .rct-main-time-header {
  height: var(--calendarHorizontalLineHeight) !important;
}

.rct-dateHeader {
  border: 0 !important;
  background-color: #ffffff !important;
}

.rct-sidebar,
.rct-sidebar-row,
.rct-vertical-lines,
.rct-hl-even,
.rct-hl-odd {
  border-color: var(--tableGridColor) !important;
  border-width: 1px;
}

.rct-item {
  border-radius: var(--borderRadius);
  font-weight: bold;
  border: 0 !important;
}
